/// <reference path="../worker.d.ts" />
// eslint-disable-next-line import/no-webpack-loader-syntax
import Worker from 'worker-loader!../worker';
import * as ComLink from 'comlink';
import {
    BasicContextInterface,
    ConsoleListener,
    ImageListener,
    InputHandler,
    ListListener,
    StateListener,
} from '../interfaces';

class WebWorkerRunner {
    private worker: Worker | null = null;
    private _context: BasicContextInterface | null = null;
    private _contextProxy: BasicContextInterface | null = null;

    get context(): BasicContextInterface {
        return this._contextProxy!;
    }

    constructor() {
        this.initialize();
    }

    initialize() {
        this.worker = Worker();
        this._context = ComLink.wrap<any>(
            this.worker
        ) as unknown as BasicContextInterface;

        this._context.console.setInputHandler(
            ComLink.proxy((message?: string) =>
                Promise.resolve(prompt(message) || '')
            )
        );

        this._contextProxy = {
            console: {
                addListener: (cb: ConsoleListener) =>
                    this._context!.console.addListener(ComLink.proxy(cb)),
                print: (m: string) => this._context!.console.print(m),
                clear: () => this._context!.console.clear(),
                setInputHandler: (cb: InputHandler) => null,
            },
            image: {
                addListener: (cb: ImageListener) =>
                    this._context!.image.addListener(ComLink.proxy(cb)),
            },
            runProgram: (p: string) => this._context!.runProgram(p),
            runStatement: (p: string) => this._context!.runStatement(p),
            stop: () => {
                this.worker!.terminate();
                this.initialize();
            },
            state: {
                addListener: (cb: StateListener) =>
                    this._context!.state.addListener(ComLink.proxy(cb)),
            },
            list: {
                addListener: (cb: ListListener) =>
                    this._context!.list.addListener(ComLink.proxy(cb)),
            },
        };
    }
}

export default WebWorkerRunner;
